import Vue from 'vue'
import App from './App.vue'
import router from '@/Router.js'
import Vuex from 'vuex'
import axios from 'axios'
import VueScrollTo from 'vue-scrollto'
 
Vue.use(VueScrollTo)
 
// You can also pass in the default options
Vue.use(VueScrollTo, {
     container: "body",
     duration: 500,
     easing: "ease",
     offset: 0,
     force: true,
     cancelable: true,
     onStart: false,
     onDone: false,
     onCancel: false,
     x: false,
     y: true
 })


Vue.config.productionTip = false
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    repos: {},
    userInfo: {},
    // updated: ;
  },
  mutations: {
    UPDATE_REPOS (state, repos) {
        state.repos = repos
    },
    UPDATE_INFO (state, userInfo) {
        state.userInfo = userInfo
    }
  },
  actions: {
    updateRepos({ commit }) {
      return new Promise((resolve, reject) => {
        let repos = {};
        let array = {};
        axios.get("https://api.github.com/users/thekidcoderdev/repos")
        .then((response) => {
          response = response.data
          for (let index = 0; index < response.length; index++) {
            array = {
              name: response[index].name,
              url: response[index].html_url,
              language: response[index].language,
              archived: response[index].archived,
              licence: response[index].licence,
              forks: response[index].forks,
              watchers: response[index].watchers
            }
            repos[response[index].name] = array
          }
          commit('UPDATE_REPOS', repos)
          resolve(repos)
        })
      })
    },
    updateInfo({ commit }) {
      return new Promise((resolve, reject) => {
        let userInfo = {};
        console.log()
        axios.get("https://api.github.com/users/thekidcoderdev")
        .then((response) => {
          response = response.data
          userInfo = {
            "username": "TheKidCoderDev",
            "pfp": response.avatar_url,
            "url": response.html_url,
            "name": response.name,
            "repos": response.public_repos,
            "gists": response.public_gists,
            "followers": response.followers,
          }
          commit('UPDATE_INFO', userInfo)
          resolve(userInfo)
        })
      })
    }
  },
  getters: {
    getRepos (state) {
      return state.repos
    },
    getInfo (state) {
      return state.userInfo
    }
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
