                console.log("getURL")
                axios.get(`https://kidcoder.dev/getURL/${Object.keys(this.$route.query)[0]}`)
                .then(res => {
                    console.log(res)
                    if(/https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/.test(res.data)) {
                        window.location.href = `${res.data}`
                    } else {
                    window.location.href = `https://${res.data}` 
                    }
                })
            }
        },
        computed: 
