<template>
    <canvas id="particles-js-canvas-el"></canvas>
</template>

<script>
import Particles from 'particlesjs'
export default {
  name: 'Particles',
  data () {
    return {
      used: false
    }
  },
  mounted: function () {
    this.startMove()
  },
  methods: {
    startMove: function () {
      Particles.init({
        selector: '#particles-js-canvas-el',
        connectParticles: false,
        sizeVariations: 5,
        speed: .25,
        minDistance: 50,
        maxParticles: 200,
        retina_detect: true,
        color: 'white',
        
        responsive: [
          {
            breakpoint: 768,
            options: {
              maxParticles: 100,
              speed: 0.25
            }
          },
          {
            breakpoint: 425,
            options: {
              maxParticles: 75,
              speed: 0.25
            }
          },
          {
            breakpoint: 320,
            options: {
              maxParticles: 0 // disables particles.js
            }
          }
        ]
      })
    }
  }
}
</script>

