<template>
  <div>
    <nav v-if="!isMobile" class="nav dt w-100 border-box pa3 ph5-ns spacecadet fixed shadow-2">
      <div class="dtc v-mid w-75 tl">
        <router-link v-for="(tab, index) in navbarRight" v-scroll-to="tab.path" @click.native="scrollMeTo(tab.path)" class="no-underline dim white ttu tracked lh-copy ph4 b dib pa2" :key="index">
          {{ tab.title }}
        </router-link>
      </div>
      <Social/>
    </nav>

    <div class="cf" v-else>
      <div class="menu">
        <button @click="menuClick" class="nav-tgl" type="button" aria-label="toggle menu">
          <!-- this span just for the three dividers in the hamburger button-->
          <span aria-hidden="true"></span>
        </button>

        <div v-if="active" class="nav absolute w-100"></div>
        <div v-if="active" class="pt4 fourtwo pb3 zl3 threenine">
          <router-link v-for="(tab, index) in navbarRight" :to="tab.path" @click.native="scrollMeTo(tab.path)" :key="index" class="tdn">
            <div class="nav-item zl3 relative pa2 w-100 tc f2 white ">{{ tab.title }}</div>
          </router-link>
        </div>
      </div>
  </div>
    
  </div>
</template>

<script>
import Social from "@/components/Social"

export default {
  name: 'NavBar',
  mounted() {
     window.addEventListener('resize', this.handleResize)
     this.handleResize()
  },
  methods: {
    handleResize (event) {
      this.window.height = document.documentElement.clientHeight
      this.window.width = document.documentElement.clientWidth
    },
    menuClick: function() {
      const menu = document.querySelector('.menu')
      if (menu.className.indexOf('active') === -1) {
              menu.classList.add('active');
              this.active = true
      } else {
              menu.classList.remove('active');
              this.active = false
      }
    },
    scrollMeTo(section) {
      document.getElementById(section).scrollIntoView("smooth");
    }
  },
  beforeDestroy: function () {
    window.removeEventListener('resize', this.handleResize)
  },
  computed: {
    // a computed getter
    isMobile() {
      // console.log(this.window.width, this.window.height)
      return this.window.width <= 450 && this.window.height <= 850 || this.window.width <= 850 && this.window.height <= 450
    }
  },
  data: function() {
      return {
        window: {
          width: 0,
          height: 0
        },
        mobile: false,
        active: false,
        welcome: 'Welcome Vue Devs!',
        logoFull: '@/assets/logoFull.svg',
        navbarRight: [
          { title: 'Home', path: 'home' },
          { title: 'About', path: 'about' },
          { title: 'Code', path: 'code' },
          { title: 'Contact', path: 'contact'},
          // { title: 'URL Shortener', path: 'shortener'}
        ],
      }
  },
  components: {
    Social
  }
}
</script>

