<template>
    <div id="contact" class="contact w-100 white">
        <div class="mail mt4 f2 w-100">
            <div class="dt center">
                <div class="b dtc v-mid pr2 tc center">Contact</div> 
                <a class="v-mid dtc " href="mailto:kidcoderdev@gmail.com">
                    <img class="dtc v-mid email-pic dim ml3" src="@/assets/outbox.white.png">
                </a>
            </div>
        </div>
        <form v-if="!sent" class="mt5 w-50-ns w-80 tc center">
            <div class="name dib-ns w-49-ns w-90">
                <input v-model="name" class="input" placeholder="Name" type="text" name="name" id="name">
            </div>

            <div class="email dib-ns w-49-ns w-90">
                <input v-model="email" class="input" placeholder="Email" type="email" name="email" id="email">
            </div>

            <div class="subject w-100-ns w-90 ">
                <input v-model="subject" class="input" type="text" name="topic" id="subject" placeholder="Subject">
            </div>

            <div class="message dib-ns w-100-ns w-90 ">
                <input v-model="message" class="input" type="text" name="message" id="message" placeholder="I'd like to chat about...">
            </div>

            <div @click="submit" class="grow pa2 shadow-4 submit mt4 br2 center mb5 white">
                Submit
            </div>
        </form>
        <div v-else class="mt5 w-80 tc center woah comeInStyle">
            <div class="pa2 shadow-4 submit mt4 br2 center mb2 black">
                Thanks for reaching out!
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
// import Particles from '@/components/Particles'
export default {
    name: "Contact",
    data: function() {
        return {
            name: null,
            email: null,
            subject: null,
            message: null,
            sent: false
            }
    },
    methods: {
        validData() {
            return this.name && this.subject && this.message && this.emailValid(this.email)
        },
        emailValid(email) {
            return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)
        },
        submit: function() {
            let that = this
            let req = {} 
            if(this.validData()) {
                req = {
                    name: this.name,
                    email: this.email,
                    subject: this.subject, 
                    message: this.message
                }

                axios.post('https://kidcoder.dev/t', req)
                .then(function (response) {
                    console.log(response);
                    that.sent = true;
                })
                .catch(function (error) {
                    console.log(error);
                })
            } else {
                alert('Invalid Data. Please fix!')
            }
            console.log(req)

        }
    },
    //components: { Particles }
}
</script>

