<template>
    <div id="code"  class="white mt4">
        <h1>Code</h1>
        <!--<div class="pb2 pl2 pr2">
            <div v-if="userInfo != null">
                <GitHeader :info="userInfo" />
            </div>
        </div>-->
        <div id="flex flex-wrap">
            <div v-for="(repo, i) in repos" :key="i" class="dib pa3">
                <RepoCard :repo="repo" />
            </div>
        </div>
    </div>
</template>

<script>
import GitHeader from '@/components/GitHeader'
import RepoCard from '@/components/RepoCard'
import { mapGetters, mapActions } from 'vuex'
export default {
    name: "Code",
    data: function() {
        return {
            repos: [],
            userInfo: null
        }
    },
    methods: {
        ...mapGetters([
            'getRepos',
            'getInfo'
        ]),
        ...mapActions([
            'updateRepos',
            'updateInfo'
        ])
    },
    mounted () {
        this.updateRepos().then((repos) => {
            this.repos = repos
        })

        this.updateInfo().then((info) => {
            this.userInfo = info
        })
    },
    components: { GitHeader, RepoCard }
}
</script>

<style scoped>
#code {
    z-index: 3;
}

</style>